import { createReducer } from '@reduxjs/toolkit';
import { requestOrderStatuses, requestOrderStatusesError, requestOrderStatusesSuccess } from './actions';
import { OrderStatusState } from '../../types/OrderStatus';

const orderStatusReducer = createReducer<OrderStatusState>({
  orderStatus: null,
  loading: false,
  error: null,
}, {
  [requestOrderStatuses.type]: (state) => ({
    ...state,
    orderStatus: null,
    loading: true,
    error: null,
  }),
  [requestOrderStatusesSuccess.type]: (state, { payload }) => ({
    ...state,
    orderStatus: payload,
    loading: false,
    error: null,
  }),
  [requestOrderStatusesError.type]: (state, { payload }) => ({
    ...state,
    orderStatus: null,
    loading: false,
    error: payload,
  }),
});

export default orderStatusReducer;
